body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 100%;
	min-height: 100vh;
	background-color: #f5f5f5;
	display: flex;
	align-items: center;
	justify-content: center;
}

.login {
  width: 700px;
	height: 500px;
	border-radius: 10px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
		0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
  /* text-align: left; */
  background-color: #667b68;
  color: #fceee9;
}

.register {
  width: 700px;
	height: 600px;
	border-radius: 10px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
		0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
  /* text-align: left; */
  background-color: #667b68;
  color: #fceee9;
}

.message {
  margin-top: 100px;
  color: orange;
}

form {
  margin-top: 0px;
}

input {
	outline: none;
	border: none;
	width: 270px;
	padding: 15px;
	border-radius: 10px;
	background-color: #fceee9;
	font-size: 14px;
  margin-right: 20px;
  margin-bottom: 30px;
}

button {
  box-sizing: border-box;
  text-decoration: none;
  border: none;
  background-color: transparent;
  font-family: inherit;
  padding: 0;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #fceee9;
  color: #667b68;
  border-radius: 8px;
  padding: 8px 16px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.18);
}